/* Layout
-----------------------------------------------------------------------------*/
body {
  margin: 8px;
  color: #ddd;
  background: #000;
  display: flex;
  justify-content: center; }

#wrapper {
  width: 1024px;
  max-width: 1024px;
  display: grid;
  grid-template-columns: 1024px;
  grid-template-areas: "header " "tabs-container" "footer "; }

.react-tabs-container {
  grid-area: tabs-container;
  width: 1024px;
  max-width: 1024px;
  display: grid;
  grid-template-columns: 100%;
  grid-template-areas: "tabs" "panel-main"; }

#tabs {
  height: 30px;
  grid-area: tabs;
  background-color: #000;
  padding: 0; }

.main-tab-panel {
  grid-area: "panel-main";
  max-width: 1024px;
  display: grid;
  grid-template-columns: 245px 559px 220px;
  grid-template-areas: "lsidebar content rsidebar"; }

.probe-tab-panel {
  background: #333;
  grid-area: "panel-main";
  max-width: 1024px;
  display: grid;
  grid-template-columns: 804px 220px;
  grid-template-areas: "content rsidebar"; }

/* Misc
-----------------------------------------------------------------------------*/
:focus {
  outline: none; }

::-moz-focus-inner {
  border: 0; }

.moneySymbol {
  color: #29b516; }

.knowledgeSymbol {
  color: #b55b16; }

.prestigeSymbol {
  color: #b51629; }

/* Header
-----------------------------------------------------------------------------*/
#header {
  height: 60px;
  grid-area: header;
  background-color: #000;
  padding: 5px; }

.header-wrapper {
  height: 60px;
  display: grid;
  grid-template-columns: 40% 20% 40%; }

.income {
  font-size: 1rem;
  /* width: 300px; */
  display: grid;
  grid-template-columns: 30% 30% 40%;
  grid-template-rows: 30px 30px;
  float: left; }

.income-header {
  grid-column: 1;
  padding: 0 10px;
  margin: auto 0 auto auto; }

.income-money {
  grid-column: 2;
  grid-row: 1;
  display: flex;
  align-items: center;
  justify-content: left; }

.income-earning {
  grid-column: 3;
  grid-row: 1;
  display: flex;
  align-items: center;
  justify-content: left; }

.income-knowledge {
  grid-column: 2;
  grid-row: 2;
  display: flex;
  align-items: center;
  justify-content: left; }

.income-learning {
  grid-column: 3;
  grid-row: 2;
  display: flex;
  align-items: center;
  justify-content: left; }

.prestige {
  font-size: 1rem;
  display: grid;
  grid-template-columns: 110px 110px 150px;
  grid-template-rows: 30px 30px;
  float: right; }

.prestige-header {
  grid-column: 1;
  padding: 0 10px;
  margin: auto 0 auto auto; }

.prestige-units {
  grid-column: 2;
  grid-row: 1;
  display: flex;
  align-items: center;
  justify-content: left; }

.prestige-earning {
  grid-column: 3;
  grid-row: 1;
  display: flex;
  align-items: center;
  justify-content: left; }

.prestige-boost {
  grid-column: 2;
  grid-row: 2;
  display: flex;
  align-items: center;
  justify-content: left; }

/* tabs
-----------------------------------------------------------------------------*/
.tab-list {
  height: 26px;
  margin: 0;
  padding: 0 15px;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  border-bottom: 2px solid #4d4dff; }

.tab-list-item {
  display: inline-block;
  width: 10em;
  text-align: center;
  margin: 0 15px;
  font-weight: bold;
  background: #0f0f56;
  border: 1px solid #4d4dff;
  border-bottom: none;
  bottom: -1px;
  padding: 2px 5px;
  cursor: pointer;
  color: #888; }

.react-tabs__tab--selected {
  background: #4d4dff;
  color: #ddd; }

.react-tabs__tab-panel {
  display: none; }

.react-tabs__tab-panel--selected {
  display: grid; }

/* footer
-----------------------------------------------------------------------------*/
#footer {
  height: 25px;
  grid-area: footer;
  background-color: #000;
  padding: 5px;
  border-top: 1px solid #4d4dff;
  opacity: 0.3;
  -webkit-transition: opacity 1s ease 0.3s;
  transition: opacity 1s ease 0.3s; }

#footer:hover {
  opacity: 1;
  -webkit-transition: opacity 0.5s ease 0.1s;
  transition: opacity 0.5s ease 0.1s; }

