.Dropdown-root {
  position: relative;
  z-index: 2;
  width: 70%; }

.Dropdown-root:hover {
  box-shadow: inset 0 0 0 3px #b51629; }

.Dropdown-control {
  position: relative;
  overflow: hidden;
  background-color: #000;
  border: 1px solid #b51629;
  border-radius: 2px;
  box-sizing: border-box;
  color: #ddd;
  cursor: default;
  outline: none;
  padding: 5px 25px 5px 10px;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease; }

.Dropdown-control:hover {
  box-shadow: inset 0 0 0 3px #b51629; }

.Dropdown-arrow {
  border-color: #999 transparent transparent;
  border-style: solid;
  border-width: 5px 5px 0;
  content: ' ';
  display: block;
  height: 0;
  margin-top: -ceil(2.5);
  position: absolute;
  right: 10px;
  top: 14px;
  width: 0; }

.is-open .Dropdown-arrow {
  border-color: transparent transparent #999;
  border-width: 0 5px 5px; }

.Dropdown-menu {
  background-color: #000;
  border: 1px solid #b51629;
  box-shadow: 0 3px 3px 3px #000;
  box-sizing: border-box;
  margin-top: -1px;
  max-height: 250px;
  overflow-y: auto;
  position: absolute;
  top: 100%;
  width: 100%;
  z-index: 1000;
  -webkit-overflow-scrolling: touch; }

.Dropdown-menu .Dropdown-group > .Dropdown-title {
  padding: 8px 10px;
  font-weight: bold;
  text-transform: capitalize; }

.Dropdown-option {
  box-sizing: border-box;
  cursor: pointer;
  display: block;
  padding: 5px 10px;
  font-weight: bold;
  font-size: 1rem; }

.Dropdown-option:last-child {
  border-bottom-right-radius: 2px;
  border-bottom-left-radius: 2px; }

.Dropdown-option:hover {
  background-color: #b51629;
  color: white; }

.Dropdown-option.is-selected {
  font-weight: bold; }

.Dropdown-noresults {
  box-sizing: border-box;
  color: #ccc;
  cursor: default;
  display: block;
  padding: 8px 10px; }

.is-selected {
  font-weight: bold;
  font-size: 1rem; }

