.text-effect {
  overflow: hidden;
  position: relative;
  width: 100%;
  text-align: center;
  margin: auto;
  -webkit-filter: contrast(110%) brightness(190%);
          filter: contrast(110%) brightness(190%); }

.neon {
  position: relative;
  background: black;
  color: transparent; }
  .neon::before, .neon::after {
    content: attr(data-text);
    color: white;
    -webkit-filter: blur(0.02em);
            filter: blur(0.02em);
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none; }
  .neon::after {
    mix-blend-mode: difference; }

.gradient,
.spotlight {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  pointer-events: none;
  z-index: 10; }

.gradient {
  background: linear-gradient(45deg, #4d4dff, #49fb35);
  mix-blend-mode: multiply; }

.spotlight {
  -webkit-animation: light 60s infinite linear;
          animation: light 60s infinite linear;
  background: radial-gradient(circle, white, transparent 25%) 0 0/25% 25%, radial-gradient(circle, white, black 25%) 50% 50%/12.5% 12.5%;
  top: -100%;
  left: -100%;
  mix-blend-mode: color-dodge; }

@-webkit-keyframes light {
  100% {
    -webkit-transform: translate3d(50%, 50%, 0);
            transform: translate3d(50%, 50%, 0); } }

@keyframes light {
  100% {
    -webkit-transform: translate3d(50%, 50%, 0);
            transform: translate3d(50%, 50%, 0); } }

.neon {
  font-size: 40px;
  text-align: center;
  margin: 0; }
  .neon:focus {
    outline: none;
    border: 1px dotted white; }

